// components
import IconArrow from '../../icons/IconArrow';
import CustomButton from '../CustomButton';

function Pagination(props: TPagination): JSX.Element {
  const { array, current, pages, setPage } = props;

  return (
    <>
      <CustomButton disabled={current === 1} onClick={() => setPage(1)}>
        1
      </CustomButton>
      <CustomButton
        disabled={current === 1}
        onClick={() => setPage(current - 1)}
      >
        <IconArrow />
      </CustomButton>
      {array.map((el) => (
        <CustomButton
          key={el}
          actived={el === current}
          onClick={() => setPage(el)}
        >
          {el}
        </CustomButton>
      ))}
      <CustomButton
        style={{ transform: 'scaleY(-1)' }}
        disabled={current === pages}
        onClick={() => setPage(current + 1)}
      >
        <IconArrow />
      </CustomButton>
      <CustomButton disabled={current === pages} onClick={() => setPage(pages)}>
        {pages}
      </CustomButton>
    </>
  );
}

export default Pagination;
