import { forwardRef } from 'react';

// components
import IconClose from '../../icons/IconClose';
import IconSearch from '../../icons/IconSearch';
import CustomButton from '../CustomButton';

// styles
import styles from './styles.module.scss';

const Search = forwardRef<HTMLInputElement, TSearch>((props, ref) => {
  const { current, onChange, onFucus, onSet, value } = props;

  return (
    <div className={styles.search}>
      <CustomButton
        size="small"
        shadow="none"
        actived
        onClick={onSet}
        disabled={value === current}
      >
        <IconSearch />
      </CustomButton>
      <input
        ref={ref}
        type="text"
        placeholder="name..."
        value={value}
        onChange={(e) => onChange(e.target.value.replace(/[-\d ]/i, ''))}
        onKeyDown={(e) => {
          if (e.key === 'Enter' && value !== current) onSet();
          if (e.key === 'Delete' && value) {
            onChange('');
            onFucus();
          }
        }}
      />

      <button
        type="button"
        className={styles.reset}
        disabled={!value}
        onClick={() => {
          onChange('');
          onFucus();
        }}
      >
        <IconClose />
      </button>
    </div>
  );
});

export default Search;
