// components
import Card from '../Card';

function Cards(props: TCards): JSX.Element {
  const { characters, current, setId } = props;

  return (
    <div>
      {characters.map((el) => (
        <Card
          key={el.id}
          id={el.id}
          name={el.name}
          image={el.image}
          setId={setId}
          current={current}
        />
      ))}
    </div>
  );
}

export default Cards;
