import { useState } from 'react';

// components
import loader from '../../../assets/loader.svg';

// styles
import styles from './styles.module.scss';

function Card(props: TCard): JSX.Element {
  const { setId, id, current, image, name } = props;

  const [isLoad, setLoad] = useState(false);

  return (
    <button
      type="button"
      className={styles.card}
      onClick={() => setId(id)}
      disabled={id === current}
    >
      <img
        onLoad={() => setLoad(true)}
        src={isLoad ? image : loader}
        alt={`avatar_${id}`}
      />
      <p>{name}</p>
    </button>
  );
}

export default Card;
