import { createSlice, PayloadAction } from '@reduxjs/toolkit';

interface ParamsState {
  [key: string]: string;
}

const searchParams = new URLSearchParams(window.location.search);

const initialState: ParamsState = {
  search: searchParams.get('search') || '',
  page: searchParams.get('page') || '',
  status: searchParams.get('status') || '',
  gender: searchParams.get('gender') || '',
  character: searchParams.get('character') || '',
};

const paramsSlice = createSlice({
  name: 'params',
  initialState,
  reducers: {
    setSearchParams: (state, { payload }: PayloadAction<ParamsState>) => {
      const { location, history } = window;
      const url = new URL(location.toString());

      Object.keys(payload).forEach((el) => {
        if (payload[el]) url.searchParams.set(el, payload[el]);
        else url.searchParams.delete(el);

        /* eslint-disable */
        state[el] = payload[el];
      });

      history.pushState({}, '', url);
    },
  },
});

export default paramsSlice;
