// redux
import useAppDispatch from '../../hooks/useAppDispatch';
import useAppSelector from '../../hooks/useAppSelector';

// components
import Cards from '../dumb/Panels/Cards';

function CardsControl({ characters, setOpen }: TCardsControl): JSX.Element {
  const character = useAppSelector((s) => s.params.character);
  const { setSearchParams } = useAppDispatch();

  const setId = (id: string): void => {
    setSearchParams({ character: id });
    setOpen();
  };

  return <Cards characters={characters} setId={setId} current={character} />;
}

export default CardsControl;
